import React from "react";
import { NavLink, Link } from "react-router-dom";
import classNames from 'classnames';
import uuid from 'react-uuid';

import {getAuthToken, getAuthTrainerId} from "../Util/Authentication";
import {Icon} from "../asset/js/icon";

class Navigation extends React.Component {
	constructor(props) {
		super(props);
		this.state={
			centerName : '',
			userImage: {
				imageSrc:  'data:image/png;base64,' ,
				setImageSrc: '',
			},
			manageList: [
				{
					title:'스케줄', icon: <Icon.ic24Schedule/>, router: '/schedule/member',
					subMenu: [
						{title:'내 회원', router: '/schedule/member', visibility: true},
						{title:'트레이너', router: '/schedule/trainer', visibility: true},
					]
				},
				{
					title:'회원 관리', icon: <Icon.ic24MemberManage/>, router: '/manage',
					subMenu: [
						{title:'회원 목록', router: '/manage', visibility: false},
						{title:'회원 등록', router: '/manage/register', visibility: false},
						{title:'수업 목록', router: '/manage/detail', visibility: false},
						{title:'수업 상세', router: '/manage/class', visibility: false},
					]
				},
			],
		};

	}

	onLogout = () => {
		localStorage.clear();
		window.location.replace('/login/admin');
		// window.location.reload(false);
	}

	render() {
		const {centerName, userImage, manageList} = this.state;

		return (
			<>
				<div className={'aside'}>
					<div className={'aside_header'}>
						<h1 className={'company'}>
							<Link to={'/login'}>
								{ <Icon.logoSmallGo/>}
								{ <Icon.logoSmallGym/>}
								{ <Icon.logoSmallAdmin className={'logo_admin'} />}
							</Link>
						</h1>
						<div className={'profile'}>
							<div className="thumb_area">
								<div className={'thumbnail'}>
									<img src={ userImage.setImageSrc !== '' ? userImage.setImageSrc : userImage.imageSrc} alt=""/>
								</div>

								<input type="file" id={'userProfile'} className={'input_file'} onChange={(e) => { this.encodeFileToBase64(e.target.files[0]);}} />
								<label htmlFor="userProfile" className={'label_file'}> <Icon.ic12Pencil/> </label>
							</div>

							<div className={'information'}>
								<div className={'name'}>
									{getAuthTrainerId() || '로그인 해주세요.'}
								</div>
								<p className={'description'}>
									{/*{getAuthTrainerId() || '로그인 해주세요.'}*/}
								</p>
								{
									getAuthTrainerId() &&
									<button type={'button'} className={'btn_setting'} onClick={this.onLogout}>
										<Icon.ic14Setting/> 로그아웃
									</button>
								}

							</div>
						</div>
					</div>
					<div className={'aside_content'}>
						<ul className={'menage_list'} key={uuid()}>
							{manageList.map((value, index) =>
								<li key={`manage-${value.router}`} className={'item'}>
									<div className="navigation">
										{ value.subMenu &&
										<>
											{value.subMenu.map((value, index) =>
												<NavLink to={value.router} className={classNames('sub_link', {'hidden': !value.visibility})} key={uuid()}>
													{value.title}
												</NavLink>
											)}
										</>
										}
										<NavLink to={value.router} activeClassName={'active'} className={'main_link'}  exact>
											{value.icon}
											{value.title}
										</NavLink>
									</div>
								</li>
							)}
						</ul>
					</div>
				</div>
			</>
		);
	}
}

export default Navigation;
