import React from "react";
import { Link, Redirect, withRouter } from 'react-router-dom';
import classNames from 'classnames';
import {Icon} from "../../asset/js/icon";

import Calenders from '../../components/Calendars';
import {getAuthToken, getUserData} from '../../Util/Authentication';
import {dateFormatYYYYMMDD, dateFormatWithTime} from '../../Util/DateFormat';
import axios from "axios";

class Schedule extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			centerImage: {
				imageSrc:  'data:image/png;base64,' ,
				setImageSrc: '',
			},
			userImage: {
				imageSrc:  'data:image/png;base64,' ,
				setImageSrc: '',
			},
			taskList: [
				// {'date': '2022. 11. 21 09:00', 'name': '한예슬'},
			],
			membership: false,
			loginMenuOpen: false,
			userId: '',
		}
	}

	makeTaskList = (dataList) => {
		console.log(dataList);
	}

	componentDidMount() {
		this.getUserNameReservationApi(getUserData().user_id);
	}

	onLoginMenuOpen = () => {
		this.setState({
			loginMenuOpen: !this.state.loginMenuOpen
		})
	}

	onLogout = () => {
		localStorage.clear();
		window.location.reload(false);
	}

	render() {
		const {userImage, centerImage, taskList, membership, loginMenuOpen} = this.state;
		if(!getAuthToken()) {
			return <Redirect replace to="/login/user" />;
		}
		return (
			<div id={'wrap'} className={classNames('user_home_wrap')}>
				<div className={'header'}>
					<div className={'inner'}>
						<div className={'logo'}>
							{ <Icon.logoGo/>}
							{ <Icon.logoGym/>}
						</div>
						<div className={'user_info'}>
							<img src={ userImage.setImageSrc !== '' ? userImage.setImageSrc : userImage.imageSrc} alt="" className={'thumbnail'}/>
							<strong className={'name'}>{getUserData().user_name}</strong>
							<div className={'more_area'}>
								<button className={classNames('btn_more', {'open': loginMenuOpen})} type={'button'} onClick={this.onLoginMenuOpen}><Icon.ic16BulletArrowDown/></button>
								<div className={'ly_menu'}>
									<button type={'button'} className={'btn_menu'} onClick={this.onLogout}>로그아웃</button>
								</div>
							</div>
						</div>
					</div>
				</div>
				<div className={'content'}>
					<div className={classNames('section', 'section_center')}>
						<div className={'center_info'}>
							<div  className={'thumbnail'}>
								<img src={ centerImage.setImageSrc !== '' ? centerImage.setImageSrc : centerImage.imageSrc} alt=""/>
							</div>
							<div className={'detail'}>
								<strong className={'name'}>Bell GYM</strong>
								<p className={'address'}>서울특별시 강남구 서초 2동 어디어디 헬스장 위치 정보</p>
							</div>
						</div>
						<div className={classNames('center_card', {'expired' : !membership})}>
							<em className={'state'}>회원권 {membership ? '사용중' : '종료'}</em>
							<div className={'period'}>
								<strong className={'num'}>D-{membership ? `123` : '0'}</strong>남음
								<p>기간 : 22.06.31 - 23.03.24</p>
							</div>
							<div className={'trainer'}>
								홀딩 2회 가능 <br/>
								담당 트레이너 : 홍길동
							</div>
						</div>
					</div>
					<div className={classNames('section', 'section_calender')}>
						<Calenders taskList={taskList}/>
					</div>
					<div className={'section'}>
						<strong className={'header_area'}>운동 일정</strong>
						<ul className={'sort_list'}>
							<li className={'item'}>
								<input id={'sort_all'} type="radio" name={'sort'}/>
								<label htmlFor="sort_all">전체</label>
							</li>
							<li className={'item'}>
								<input id={'sort_planned'} type="radio" name={'sort'}/>
								<label htmlFor="sort_planned">예정</label>
							</li>
							<li className={'item'}>
								<input id={'sort_finish'} type="radio" name={'sort'}/>
								<label htmlFor="sort_finish">완료</label>
							</li>
						</ul>

						<div className={'task_list'}>
							{taskList.map((value, index) =>
								<div className={'item'}>
									{dateFormatYYYYMMDD(new Date(value.date), '.')}
								</div>
							) }
						</div>
					</div>
				</div>
			</div>
		);
	}

	getUserNameReservationApi = async (value) => {
		try{
			// console.log(value);
			const param = JSON.parse(JSON.stringify({
				user_phone: value
			}));
			console.log(param);
			const requestOption ={
				method: 'POST',
				headers: {
					'Content-Type': 'application/json',
					'Cache-Control': 'no-cache',
					'Accept': 'application/json',
					Authorization: `Bearer ${getAuthToken()}`,
				},
			};
			await axios.post("http://gogym.co.kr:8080/api/auth/reservation/all/user",
				JSON.stringify(param), requestOption )
				.then(res =>{
					const resData = JSON.parse(JSON.stringify(res.data));
					axios.defaults.headers.common['Authorization'] = `Bearer ${getAuthToken()}`;
					this.makeTaskList(resData.data);
				})
				.catch(ex=>{
					console.log("login requset fail : " + ex);
					// console.log(ex.response.status);
				})
				.finally(()=>{console.log("login request end")});
		}catch(e){
			console.log(e.response);
		}
	}

};
// export default Schedule;
export default withRouter(Schedule);